body {
    direction: rtl;
}

.num-stats {
    direction: ltr;
}

.lg-outer {
    direction: ltr;
}

.text-end {
    text-align: left !important;
}

/*______________________ Header  ___________________________*/


header {



    .menu {


        @media (max-width: 991.98px) {

            right: -300px;
            left: auto;

            &.active {
                right: 0;
                left: auto;
            }



        }

    }

}

.dropdown-toggle {

    &:after {
        width: 8px;
        height: 6px;
        border: 0;
        background-image: url('data:image/svg+xml,<svg width="8" height="6" viewBox="0 0 8 6" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0.5 1L4 5L7.5 1" stroke="black" stroke-linecap="round"/></svg>');
        background-position: center;
        background-repeat: no-repeat;
        margin: 0 6px;
        vertical-align: middle;
    }
}

/*______________________ Slider  ___________________________*/

.slide {
    .overlay {
        right: 0;
        left: auto;




    }

    .wrap-slide {

        right: 0;
        left: auto;




        .left-section {


            .btn {

                margin-left: 10px;
                margin-right: 0;
            }
        }
    }
}


.slick-slide {

    &:nth-child(3) {

        &.slick-active img {
            animation-name: fadeInLeftSlide;
            animation-duration: 1s;
            opacity: 1;
        }


    }
}


/*______________________Home Page ___________________________*/

.about-section {
    .video-about {


        .play-btn {

            left: 50%;
            right: auto;
            transform: translate(-50%, -50%);

        }
    }

    .btns-action {
        .btn {
            width: 170px;

            @media (max-width: 575.98px) {
                width: 150px;
            }
        }
    }



}


.features-kids-section {



    .features-list {


        .feature-item {


            i {
                margin-left: 1.25rem;
                margin-right: 0;
            }
        }
    }



}

.growth-section {
    .banners-companies {


        .c-item {

            .details {

                right: 0;
                left: auto;
                transform: translate(0, 50%);
                transition: all 2s;

                .logo {

                    right: 50%;
                    left: auto;
                    transform: translate(50%, 0);
                    transition: all 2s;

                }





            }
        }

        .company-1 {


            &:hover {
                .details {
                    transform: none;

                    .logo {

                        right: 0;
                        left: auto;
                        transform: none;


                    }

                }
            }
        }

        .company-2 {

            &:hover {
                .details {
                    transform: none;

                    .logo {

                        right: 0;
                        left: auto;
                        transform: none;


                    }


                }
            }
        }




        .effect2 {
            &:hover {
                .details {

                    transform: none;

                    .logo {


                        right: 0;
                        left: auto;
                        transform: none;


                    }


                }
            }
        }
    }
}

.features-parents-section {


    .main-description {


        ul {
            padding-right: 1rem;
            padding-left: 0;
        }


    }
}





.testimonial-section {


    .comment {

        .descripe {
            margin-right: 2.5rem;
            margin-left: 0;


        }
    }
}

.cta-wrapper {



    .txt {

        right: 50%;
        left: auto;
        transform: translate(50%, -50%);

        @media (max-width: 767.98px) {
            right: auto;
            left: auto;
            transform: none;



        }




    }



    .banner-cta {

        left: -553px;
        right: auto;

        @media (max-width: 767.98px) {
            left: auto;



        }

    }

    &:hover {

        .txt {

            right: 0;
            left: auto;

            @media (max-width: 767.98px) {
                right: auto;



            }

        }

        .download-links {


            .two-btns:nth-child(2) {

                transform: translate(360px, 0);

                @media (max-width: 767.98px) {
                    transform: none;



                }


            }


        }


        .banner-cta {
            left: 0;
            right: auto;

            @media (max-width: 767.98px) {
                left: auto
            }
        }

    }
}


/*______________________About PAge ___________________________*/
.header-section {
    .main-title {

        left: 50%;
        right: auto;

        transform: translate(-50%, -50%);

        @media (max-width: 767.98px) {
            transform: none;
            left: auto;


        }

        .action {
            .btn-primary {

                margin-left: 1rem;
                margin-right: 0;

            }


        }
    }
}

.about-overview-section {

    .statistics {
        li {


            .num {
                margin-left: 0.75rem;
                margin-right: 0;


            }


        }
    }
}

.feature-section {


    .main-banner {


        .mobile-banner {

            left: 20%;
            right: auto;
            transform: translate(-50%, -50%);

        }
    }
}

.feature-section-parents {

    .details {


        ul {
            padding-right: 1.5rem;
            padding-left: 0;


        }
    }
}

.awards-section {



    .award-item {


        .logo-award {

            margin-left: 18px;
            margin-right: 0;



        }


    }
}

.ecommerce-product {


    .social-links {

        ul {


            li {
                margin-left: 0.813rem;
                margin-right: 0;

            }
        }
    }



    .option {
        ul {
            padding-right: 2rem;
            padding-left: 0;


            li {

                padding-right: 1rem;
                padding-left: 0;

            }
        }
    }


}

/*______________________FAQ PAge ___________________________*/
.question {
    .answer {
        padding: 0 3rem 2.25rem 4rem;

        @media (max-width: 575.98px) {
            padding: 0 2rem 2.25rem 2rem;

        }

    }
}

/*______________________ download app page ___________________________*/

.downloadapp {
    .apps {
        .banner-side {
            &:before {
                left: 0;
                right: auto;
            }
        }

        &.mobile-apps {
            .banner-side {
                &:before {
                    right: 10%;
                    left: auto;
                }
            }
        }
    }
}

/*______________________ Contact  page ___________________________*/

.form-contact {
    .form-select {
        background-position: left 10px center;
    }
}

.tel {
    .btn {
        &:before {

            right: 0;
            left: auto;

        }
    }
}

/*______________________ Footer   ___________________________*/
footer {


    .pages-links {
        margin-right: 2rem;
        margin-left: 0;



    }


    .copyright {

        direction: ltr;
    }
}